/* Allows navbar and content to sit side-by-side */
.dashboardContainer {
  display: flex;
}

/* Main content styling */
.container {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  margin-left: 60px; /* Default offset for the collapsed navbar width */
  transition: margin-left 0.3s ease;
}

/* Only adjust margin when the navbar is hovered */
.navbar:hover + .container {
  margin-left: 200px; /* Matches expanded width of the navbar */
}

.title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #003366;
}

.metrics,
.navigation {
  width: 100%;
  max-width: 600px;
  padding: 1.5rem;
  margin: 1rem 0;
  background-color: #f0f4f8;
  border-radius: 8px;
  text-align: center;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
    margin-left: 0; /* Removes offset on smaller screens */
  }

  .title {
    font-size: 1.5rem;
  }
}